import axios from 'axios';

const BASE_URL = '/api';

export const fetchWeatherData = async (nx, ny, baseDate, baseTime) => {
  try {
    const url = `${BASE_URL}?pageNo=1&numOfRows=60&dataType=JSON&base_date=${baseDate}&base_time=${baseTime}&nx=${nx}&ny=${ny}`;
    console.log(`Fetching data from: ${url}`);
    const response = await axios.get(url);
    console.log('Response data:', response.data);

    if (response.data && response.data.response && response.data.response.body && response.data.response.body.items) {
      return response.data.response.body.items.item;
    } else {
      console.error('Invalid response structure:', response.data);
      throw new Error('Invalid response structure');
    }
  } catch (error) {
    console.error('Error fetching weather data:', error);
    throw error;
  }
};

export const extractWeatherInfo = (data) => {
  const now = new Date();
  const currentHour = now.getHours();
  const currentMinutes = now.getMinutes();

  let targetTime;
  if (currentMinutes < 30) {
    targetTime = `${String(currentHour).padStart(2, '0')}00`;
  } else {
    targetTime = `${String(currentHour + 1).padStart(2, '0')}00`;
  }
  console.log("targetTime:" + targetTime);
  const relevantData = data.filter(item => item.fcstTime === targetTime);
  const weatherInfo = {
    temperature: relevantData.find(item => item.category === "T1H")?.fcstValue || 'N/A',
    precipitation: relevantData.find(item => item.category === "RN1")?.fcstValue === "강수없음" ? '0.0' : relevantData.find(item => item.category === "RN1")?.fcstValue || 'N/A',
    sky: relevantData.find(item => item.category === "SKY")?.fcstValue || 'N/A',
    humidity: relevantData.find(item => item.category === "REH")?.fcstValue || 'N/A',
    precipitationType: relevantData.find(item => item.category === "PTY")?.fcstValue || 'N/A'
  };
  return weatherInfo;
};
