import React, { useEffect, useState } from 'react';
import { fetchWeatherData, extractWeatherInfo } from './WeatherService';
import {
  WeatherSunny24Filled,
  WeatherCloudy24Filled,
  Cloud24Filled,
  WeatherRain24Filled,
  WeatherRainSnow24Filled,
  WeatherSnow24Filled,
  Drop24Filled,
  WeatherDrizzle24Filled,
  WeatherSnowflake24Filled
} from '@fluentui/react-icons';

const getBaseDateTime = () => {
  const now = new Date();
  const year = now.getFullYear();
  const month = String(now.getMonth() + 1).padStart(2, '0');
  const date = String(now.getDate()).padStart(2, '0');
  const hours = now.getHours();
  const minutes = now.getMinutes();

  const baseDate = `${year}${month}${date}`;

  let baseTime;
  if (minutes < 30) {
    baseTime = `${String(hours - 1).padStart(2, '0')}30`;
  } else {
    baseTime = `${String(hours).padStart(2, '0')}30`;
  }
  console.log("baseTime:" + baseTime);
  console.log("baseDate:" + baseDate);
  return { baseDate, baseTime };
};

const WeatherDisplay = () => {
  const [weatherInfo, setWeatherInfo] = useState(null);
  const [error, setError] = useState(null);

  useEffect(() => {
    const { baseDate, baseTime } = getBaseDateTime();
    const getWeatherData = async () => {
      try {
        const data = await fetchWeatherData(99, 75, baseDate, baseTime);
        console.log('Weather data:', data); // 응답 데이터 구조 확인
        const info = extractWeatherInfo(data);
        setWeatherInfo(info);
      } catch (err) {
        setError(err.message);
      }
    };
    getWeatherData();
  }, []);

  if (error) return <div>Error: {error}</div>;
  if (!weatherInfo) return <div>Loading...</div>;

  const renderIcon = () => {
    if (weatherInfo.precipitationType !== '0' && weatherInfo.precipitationType !== 'N/A') {
      switch (weatherInfo.precipitationType) {
        case '1': return <> <WeatherRain24Filled /> 비 </>;
        case '2': return <> <WeatherRainSnow24Filled /> 비/눈 </>;
        case '3': return <> <WeatherSnow24Filled /> 눈 </>;
        case '5': return <> <Drop24Filled /> 빗방울 </>;
        case '6': return <> <WeatherDrizzle24Filled /> 빗방울/눈날림 </>;
        case '7': return <> <WeatherSnowflake24Filled /> 눈날림 </>;
        default: return null;
      }
    } else {
      switch (weatherInfo.sky) {
        case '1': return <> <WeatherSunny24Filled /> 맑음 </>;
        case '3': return <> <WeatherCloudy24Filled /> 구름 많음 </>;
        case '4': return <> <Cloud24Filled /> 흐림 </>;
        default: return null;
      }
    }
  };

  return (
    <div>
      {/* <h1>Weather Information</h1> */}
      <div>기온: {weatherInfo.temperature} ℃</div>
      <div>강수량: {weatherInfo.precipitation} mm</div>
      <div>습도: {weatherInfo.humidity} %</div>
      <div>
        {renderIcon()}
      </div>
    </div>
  );
};

export default WeatherDisplay;
